<template>
  <div id='box'>
    <div  class="top">
      <span>共 <span class="fc"> {{list.length}} </span> 个商品</span>
      <img src="@/assets/images/my/mall_cart_delete.png" alt="" class='icon' @click="del()">
    </div>
    <div class="bg">
      <van-checkbox-group v-model="result" @change='getchoose()' id="item">
        <van-checkbox v-for="(item,index) in list" :key="index" :name="item">
          <div class="item">
            <img :src="item.original_img" alt="" class="img">
            <div class="right">
              <p class="title ellipsis">{{item.goods_name}}</p>
              <span class="guige">{{item.spec_key_name}}</span>
              <div class="r_bottom">
                <span>￥{{item.member_goods_price}}</span>
                <van-stepper v-model="item.goods_num" @plus="get1(item)" @minus="get1(item)"/>
              </div>
            </div>
          </div>
        </van-checkbox>
      </van-checkbox-group>
      <div v-if="list.length==0" class="nomore"> 暂无数据 </div>
    </div>
    <div class='all'>
      <van-checkbox v-model="checked" @change='getall()'></van-checkbox>
      <span class='all_choose' @click='getall()'>全选</span>
      <div class="count">合计：<span style="color:#DC2121;font-weight:600">￥{{result.length?getcount:'0.00'}}</span></div>
      <div class="uporder" @click='gouporder()'>去 结 算</div>
    </div>
  </div>
</template>

<script>
import { Checkbox, CheckboxGroup, BadgeGroup, Stepper, Toast, Dialog } from 'vant';
import { cartList, del_cart } from '@/axios/getData'
export default {
    components: {
      vanCheckbox:Checkbox,
      vanCheckboxGroup:CheckboxGroup,
      vanStepper:Stepper,
      Dialog
    },
    props: {

    },
    data() {
      return {
        all:true,
        checked:false,   // 全选
        list: [],
        result: [],
      };
    },
    computed: {
      getcount(){
        var sum=0;
        console.log(this.result)
        this.result.forEach((item,index)=>{
          sum += Number(item.goods_num)*Number(item.goods_price)
        })
        return sum.toFixed(2)
      }
    },
    created() {
      this.getdata();
    },
    methods: {
      //  去付款
      gouporder(){
        this.$router.push({path:'/uporder', query:{
          action:'',
          item:JSON.stringify(this.result)
        }})
      },
      async getdata(){
        var res = await cartList();
        this.list = res.data
        console.log(res.data)
      },
      get1(item){
        setTimeout(()=>{
          if(JSON.stringify(this.result).search("\"id\":"+item.id)==-1){
            this.result.push(item)
          }
        },100)
      },
      // 单选
      getchoose(){
        // 关闭全选开关  若不设置开关 则会发生意想不到的结果 不能很好的实现全选单选功能
        this.all = false
        // 判断是否选完
        // console.log(this.result)
        // console.log(this.list)

        if(this.list.length == this.result.length){
          this.checked = true
        } else{
          this.checked = false
        }
        setTimeout(()=>{
          this.all = true;
        },100)
      },
      // 全选
      getall(){
        if(this.all){
          if(this.checked){
            this.result =this.list;
          } else {
            this.result = []
          }
        }
        console.log(this.result)
      },
      // 删除选中商品
      del(){
        if(!this.result.length){
          Toast('请选中要删除商品')
        } else {   //  数提交服务器  成功提交后本地删除
          var that = this
          let data = {};
          var arr = [];
          this.result.forEach((item)=>{
            arr.push(item.id)
          })
          data.cart_ids = arr

          Dialog.alert({
            title: '亲',
            message: '删除后不可恢复哦！',
            showCancelButton:true,
          }).then(() => {
            del_cart( data ).then((res)=>{
              if(that.checked){
                that.result = [];   //  清空选中列表
                that.list = [];
                Toast('删除成功!')
                return;
              }
              that.result.forEach(item0 => {  //  先循环选中列表
                that.list.forEach((item1,i) =>{  
                  if(item0.id==item1.id){
                    that.list.splice(i,1)   //  将选中列表从数据源中删除
                  }
                })
              });
              Toast('删除成功!')
              that.result = [];   //  清空选中列表
              that.checked = false;  // 全选
            })
          }).catch(() => {
            console.log('点击了取消')
        });
        }
      },
    },
    mounted() {

    },
  }
</script>

<style scoped lang="scss">
#box{
  padding:0 10pt;
  font-size: 12pt;
  height:500pt;
  overflow: scroll;
  .bg{
    border-radius: 10pt;
    background: #fff;
    padding-left:10pt;
    padding-bottom:50pt;
    // box-shadow: 
  }
}
.top{
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-bottom:10pt;
  .icon{
    width:50pt;
    height:50pt;
  }
}
.item{
  display: flex;
  padding:10pt 0;
  // border-bottom:1px solid #eee;
  .img{
    margin-right:10pt;
    width:70pt;
    height:70pt;
  }
  .right{
    display: flex;
    flex-direction: column;
    justify-content: space-around;

    .r_bottom{
      display: flex;
      align-items: center;
      justify-content: space-between; 
    }
  }
  .title{
    display: inline-block;
    width: 120pt;
  }
}
/* 底部 */
.all{
  position: fixed;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  width:100%;
  height:50pt;
  // left:-20pt;
  left:0;
  border-top:1px solid #ccc;
  bottom:0;
  padding:15pt 20pt;
  background: #fff;
  .count{
    margin-left:15pt;
  }
  .uporder{
    height:50pt;
    font-size: 14pt;
    color:#fff;
    line-height: 50pt;
    position: absolute;
    right:0;
    width:30%;
    text-align: center;
    background: #FF4C23;
  }
}
.all_choose{
  display: inline;
  padding:0 10pt;
}
.fc{
  color:#FF4C23;
}
/deep/ .van-stepper__plus,/deep/ .van-stepper__input,/deep/ .van-stepper__minus{
  width:50px !important;
}
/deep/ .van-checkbox{
  display: flex;
  align-items: center;
}

.guige{
  display: inline-block;
  color:#999;
  font-size: 8pt;
}
.nomore{
  padding-top:20pt;
  color:#999;
  font-size: 13pt;
  text-align: center;
}
</style>



